html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

:root {
  --primary-text-color: #090a0f;
  --title-text-color: #2e2f42;
  --primary-white-color: #fff;
  --hover-color: #404bbf;
  --style-color: #1b6def;
}

body {
  background-color: var(--primary-white-color);
  color: var(--primary-text-color);
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 3px solid #000;
  border-right: 3px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-left: 80px;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  width: 1200px;
  background-color: var(--primary-white-color);
  border-radius: 30px;
  margin-left: auto;
  margin-right: auto;
  padding: 25px;
  font-size: 17px;
  box-shadow: 0 7px 29px #fff;
}

.cat-cont {
  display: flex;
}

.image {
  width: 600px;
}

.breed-name {
  color: var(--title-text-color);
}

.description {
  color: var(--primary-text-color);
  min-width: 500px;
  margin-bottom: 25px;
}

.temperament {
  color: var(--primary-text-color);
}

.bold-temperament {
  color: var(--primary-text-color);
  font-weight: 600;
}

.breed-select {
  background-color: var(--style-color);
  color: #fff;
  height: 35px;
  margin-top: 20px;
  margin-left: 50px;
}

.text-cont {
  margin-left: 30px;
  margin-right: 30px;
}

